.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.login-maindiv{
  background-image: linear-gradient(#30cfd0, #c703a6);
}
.register-maindiv{
  background-image: linear-gradient(#30cfd0, #c703a6);
}
.login-div, .register-main-div{
  position: relative;
}
.logotext-register, .logotext-login{
color: white;
text-shadow: 0px 5px 5px #33697e;
}
